<template>
  <div class="news-article" v-if='newsList'>
    <div class="head df px-2 py-3 bg-gray">
      <div class="flex-1 text-info">{{newsList.name}}</div>
      <div>2015</div>
    </div>
    <div v-html='newsList.body' class="w-100 news-body px-3"></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      newsList: null
    }
  },
  props: {
    // 从router路由传过来的
    id: String
  },
  methods: {
    async fetch () {
      const res = await this.$http.get(`article/${this.id}`)
      this.newsList = res.data
    }
  },
  created () {
    this.fetch()
  }
}
</script>

<style>
.news-body img{
  width:100%;
}
</style>
