<template>
  <m-card title='新闻'>
    <div class="card-nav nav jc-between py-3">
      <div v-for='(category,i) in categories'
      :key='i' class="nav-item"
      :class='{active:i === active}'
      @click='$refs.list.swiper.slideTo(i)'
      >{{category.name}}</div>
    </div>
    <swiper :options='{autoHeight: true}' ref='list' @slide-change='()=>active=$refs.list.swiper.realIndex'>
      <swiper-slide v-for='(category,i) in categories' :key='i'>
        <slot name='items' :category='category'></slot>
      </swiper-slide>
    </swiper>
  </m-card>
</template>

<script>
export default {
  props: {
    title: {
      type: String
    },
    categories: {
      type: Array
    }
  },
  data () {
    return {
      active: 0
    }
  }
}
</script>

