    <m-list-card title='英雄' :categories='categories'>
      <!-- 取作用域插槽里的数据 -->
      <template #items='{category}'>
        <router-link tag='div' :to='"/article/"+item._id' v-for="(item, i) in category.newsList" :key='i' class="df fs-md py-2">
          <span class="text-info">{{item.categoryName}}</span>
          <span class='px-1'>|</span>
          <span class='flex-1 text-ellipsis'>{{item.name}}</span>
          <span>{{item.updatedAt | formatDay}}</span>
