<script>
// @ is an alias to /src
// 格式化时间的插件
import dayjs from 'dayjs'
export default {
  name: 'home',
  filters: {
    formatDay (val) {
      return dayjs(val).format('MM-DD')
    }
  },
  data () {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination'
        },
        autoplay: true,
        loop: true
      },
      categories: [],
      heroesList: []
    }
  },
  methods: {
    async fetchNews () {
      const res = await this.$http.get('news/list')
      this.categories = res.data
    },
    async fetchHeroes () {
      const res = await this.$http.get('heroes/list')
      this.heroesList = res.data
    }
  },
  created () {
    this.fetchNews()
    this.fetchHeroes()
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  components: {
  }
}
</script>
